import React, { Component } from "react";
import { Container } from "./NavBar.style";

export default class NavBar extends Component {
    scrollToMyRef = section => {
        section.current.scrollIntoView({
            behavior: "smooth",
            block: "start",
            inline: "nearest"
        });
    };

    render() {
        return (
            <Container>
                <span
                    onClick={() => this.scrollToMyRef(this.props.myPortfolio)}
                >
                    Portfolio
                </span>
                <span onClick={() => this.scrollToMyRef(this.props.aboutMe)}>
                    About
                </span>
            </Container>
        );
    }
}
